.myHeader {
  background-color: #d9d9d9;
  display: flex;
}
.myHeader .title {
  width: 20%;
  text-align: center;
  flex: none;
  color: #808080;
  font-size: 26px;
}
.myHeader .link {
  text-align: center;
  flex: auto;
}
.myHeader .link a {
  margin: 0px 24px;
  color: #1890ff;
}
.myHeader .poem {
  width: 20%;
  flex: none;
  letter-spacing: 3px;
}
